import React, { useState } from "react";
import {InputText, InputPassword, Button, FormControl, Card, LayoutOne} from 'upkit';
import { useForm } from "react-hook-form";
import { useHistory } from "react-router";

import StoreLogo from "../../components/StoreLogo";
import { useDispatch, useSelector } from "react-redux";
import {userLogin} from '../../features/Auth/actions';
import {rules} from './validation';
import {login} from '../../api/auth';
import { Link } from "react-router-dom";


const statusList = {
    idle : 'idle',
    process : 'process',
    success : 'success',
    error : 'error'
}

export default function Login(){
    const {register,handleSubmit,formState:{errors},setError} = useForm();
    const [status, setStatus] = useState(statusList.idle);
    const dispatch = useDispatch();
    const history = useHistory();

    const onSubmit = async ({email, password})=>{
        
        setStatus(statusList.process);

        let {data} = await login(email,password);
        if(data.error){

            setError('password',{type:'invalidCredential', message: data.message});
            setStatus(statusList.error);
        }else{

            let {user, token} = data;

            dispatch(userLogin(user, token));
			
			setStatus(statusList.success);
			
            history.push('/')
        }
    }

    return (
        <LayoutOne size="small">
            <br/>
            <Card color="white">
                <div>
                    <div className="text-center mb-5">
                        <StoreLogo/>
                    </div>

                    <form onSubmit={handleSubmit(onSubmit)}>

                        <FormControl errorMessage={errors.email?.message}>
                            <InputText placeholder="email" fitContainer {...register("email",rules.email)}/>
                        </FormControl>
                        
                        <FormControl errorMessage={errors.password?.message}>
                            <InputText placeholder="password" fitContainer {...register("password",rules.password)}/>
                        </FormControl>

                        <Button fitContainer size="large" disabled={status === "process"}>
                            Login
                        </Button>
                    </form>
                    <div className="text-center mt-2">
                        Belum punya akun? <Link to="/register"><b>Daftar sekarang </b></Link>
                    </div>
                </div>    
            </Card>
        </LayoutOne>

    )
}