import {
	START_FETCHING_PRODUCT,
	ERROR_FETCHING_PRODUCT,
	SUCCESS_FETCHING_PRODUCT,
	SET_PAGE,
	SET_KEYWORD,
	SET_CATEGORY,
	SET_TAGS,
	NEXT_PAGE,
	PREV_PAGE,
	TOGGLE_TAG
} from './constans';
import debounce from 'debounce-promise';

import {getProducts} from '../../api/product';

let debouncedFetchProducts = debounce(getProducts, 1000);

export const fetchProducts = ()=>{
	return async (dispatch, getState)=>{
		
		let perPage = getState().products.perPage || 9;
		let currentPage = getState().products.currentPage || 1;
		let tags = getState().products.tags || [];
		let keyword = getState().products.keyword || '';
		let category = getState().products.category || '';
		
		const params = {
			limit : perPage,
			skip : (currentPage * perPage) - perPage,
			q : keyword,
			tags,
			category
		}
		
		dispatch(startFetchingProducts());
		
		try{
			let {data : {data, count}} = await debouncedFetchProducts(params);
			dispatch(successFetchingProduct({data, count}));
		}catch(err){
			dispatch(errorFetchingProduct());
		}
	}
}

export function startFetchingProducts(){
	return {type : START_FETCHING_PRODUCT};
}

export function errorFetchingProduct(){
	return {type : ERROR_FETCHING_PRODUCT};
}

export function successFetchingProduct({data, count}){
	return {type : SUCCESS_FETCHING_PRODUCT, data, count};
}

export function setPage(number = 1){
	return {type : SET_PAGE, currentPage:number};
}

export function setKeyword(keyword){
	return {type : SET_KEYWORD, keyword};
}

export function setCategory(category){
	return {type : SET_CATEGORY, category};
}

export function setTags(tags){
	return {type : SET_TAGS, tags};
}

export function toggleTag(tag){
	return {type : TOGGLE_TAG, tag};
}

export function goToNextPage(){
	return {type : NEXT_PAGE};
}

export function goToPrevPage(){
	return {type : PREV_PAGE};
}