import React from 'react';
import {Link, Redirect, HashRouter as Router, Route, Switch } from 'react-router-dom';
import {Provider} from 'react-redux';
import 'upkit/dist/style.min.css';

import Home from './pages/Home';
import Register from './pages/Register';
import RegisterSuccess from './pages/RegisterSuccess';
import Login from './pages/Login';
import UserAddressAdd from './pages/UserAddressAdd';
import UserAddress from './pages/UserAddress';
import Checkout from './pages/Checkout';
import Invoice from './pages/Invoice';
import UserAccount from './pages/UserAccount';
import UserOrders from './pages/UserOrders';
import Logout from './pages/Logout';

//custom route
import GuardRoute from './components/GuardRoute';
import GuestRouteOnly from './components/GuestRouteOnly';

import store from './app/store';
import {listen} from './app/listener';
import {getCart} from './api/cart';

function App() {
  React.useEffect(()=>{
    listen();
	getCart();
  },[])

  return (
    <Provider store={store}>
      <div>
        <Router>
          <Switch>
            <GuestRouteOnly path='/register/berhasil'>
              <RegisterSuccess/>
            </GuestRouteOnly>
            <GuestRouteOnly path='/login'>
              <Login/>
            </GuestRouteOnly>
            <GuardRoute path='/alamat-pengiriman/tambah'>
              <UserAddressAdd/>
            </GuardRoute>
            <GuardRoute path='/alamat-pengiriman'>
              <UserAddress/>
            </GuardRoute>
            <GuardRoute path='/checkout'>
              <Checkout/>
            </GuardRoute>
            <GuardRoute path='/invoice/:order_id'>
              <Invoice/>
            </GuardRoute>
            <GuardRoute path='/account'>
              <UserAccount/>
            </GuardRoute>
            <GuardRoute path='/pesanan'>
              <UserOrders/>
            </GuardRoute>
            <Route path='/logout'>
              <Logout/>
            </Route>
            <GuestRouteOnly path='/register' component={Register} />
            <Route path='/'>
              <Home/>
            </Route>
          </Switch>
        </Router>
      </div>
    </Provider>
  );
}

export default App;
