import React from "react";
import { LayoutOne, Card, FormControl, InputText, Button} from "upkit";
import {useForm} from 'react-hook-form';
import {Link, useHistory} from 'react-router-dom';

import {rules} from './validation';
import {registerUser} from '../../api/auth';
import StoreLogo from "../../components/StoreLogo";

const statusList = {
    idle : 'idle',
    process : 'process',
    success : 'success',
    error : 'error'
}

export default function Register(){
	
	console.log(rules)

    let {register,handleSubmit,formState:{errors},setError} = useForm();
	console.log(register)
    let [status , setStatus] = React.useState(statusList.idle);
    let history = useHistory();

    const onSubmit = async formData=>{

        let {password_confirmation, password} = formData;

        if(password !== password_confirmation){
            return setError('password_confirmation',{type:'equality', message:'konfirmasi password harus sama dengan password'})
        }

        setStatus(statusList.process);

        let {data} = await registerUser(formData);
		
        if(data.error){
			console.log('error');
            let fields = Object.keys(data.fields);
    
            fields.forEach(field=>{
				setError(field, {type:'coba', message: data.fields[field]?.properties?.message})
            })
    
            setStatus(statusList.error);
			return;
        }
        setStatus(statusList.success);
		history.push('/register/berhasil');
    }

    return (
        <LayoutOne size="small">
            <Card color="white">
                <div>
                    <div className="text-center mb-5">
                        <StoreLogo/>
                    </div>
                    <form onSubmit={handleSubmit(onSubmit)}>
                        <FormControl errorMessage={errors.full_name?.message}>
                            <InputText {...register("full_name",rules.full_name)} placeholder="Nama Lengkap" fitContainer />
                        </FormControl>
                        <FormControl errorMessage={errors.email?.message}>
                            <InputText {...register("email",rules.email)} placeholder="email" fitContainer />
                        </FormControl>
                        <FormControl errorMessage={errors.password?.message}>
                            <InputText {...register("password",rules.password)} placeholder="Password" fitContainer />
                        </FormControl>
                        <FormControl errorMessage={errors.password_confirmation?.message}>
                            <InputText {...register("password_confirmation",rules.password_confirmation)} placeholder="Konfirmasi Password" fitContainer />
                        </FormControl>
                        <Button size="large" fitContainer>{status == statusList.process?"Sedang memproses":"Mendaftar"}</Button>
                    </form>
                    <div className="text-center mt-2">
                        Sudah punya akun? <Link to="/login"><b> Masuk Sekarang</b></Link>
                    </div>
                </div>
                
            </Card>
        </LayoutOne>
    )
}